<template>
  <div>
    <div class="search">
      <van-nav-bar
        title="我卖出的商品"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
      />
    </div>
    <div>
      <van-pull-refresh
        v-model="isLoading"
        @refresh="get_allGoods"
        :success-text="refresh_text"
        :success-duration="1000"
      >
        <div>
          <!-- 所有商品列表 -->
          <SellCard
            :goods="goodsList"
          />
        </div>
      </van-pull-refresh>
      <div class="button">
        <!-- 出售按钮 -->
        <van-button
          type="info"
          @click="sell"
          to="/Sell"
          :round="true"
          icon="plus"
        />
      </div>
    </div> 
  </div>
</template>

<script>
import Vue from 'vue';
import SellCard from '../components/SellCard'
import axios from 'axios';
import '@vant/touch-emulator';

import { Search, Toast } from 'vant';
import { Button } from 'vant';
import { PullRefresh } from 'vant';
import { Row, Col } from 'vant';
import { NavBar } from 'vant';

Vue.use(NavBar);

Vue.use(Row).use(Col);
Vue.use(PullRefresh);
Vue.use(Button);
Vue.use(Search);

export default {
    name:'MySell',
    components: {
        SellCard: SellCard,
    },
    data() {
        return {
          refresh_text:'',
          search: false,
          value: '',
          count: 0,
          isLoading: false,
          searchList: [],
          goodsList: [{"goodId":4,"userId":"001","goodName":"篮球","number":1,"price":88,"describe":"好！","phone1":null,"phone2":null,"phone3":null},{"goodId":6,"userId":"009","goodName":"足球","number":1,"price":55,"describe":"好！","phone1":null,"phone2":null,"phone3":null},{"goodId":7,"userId":"005","goodName":"电脑","number":1,"price":2000,"describe":null,"phone1":null,"phone2":null,"phone3":null},{"goodId":8,"userId":"008","goodName":"电脑","number":1,"price":3000,"describe":null,"phone1":null,"phone2":null,"phone3":null}],
        }     
    },
    mounted(){
      this.get_allGoods();
    },
    methods: {
        onClickLeft() {
            this.$router.go(-1);
        },
      get_allGoods(){
        axios.get('http://localhost:8090/androidApp/Goods/FindMyGoods')
        .then(response=>{
          // eslint-disable-next-line no-console
          console.log(response);
          this.goodsList=response.data;
          // eslint-disable-next-line no-console
          console.log("goodsList",this.goodsList);
          setTimeout(() => {
            this.refresh_text = "刷新成功！";
            this.value='';
            this.isLoading = false;
            this.search = false;
          }, 500);
        })
        .catch(error=>{
          // eslint-disable-next-line no-console
          console.log(error);
          setTimeout(() => {
            Toast("网络开小差了，请稍后再试！")
            this.refresh_text="刷新失败！";
            this.isLoading = false;
            this.search=false;
          }, 500);
        })
      },
      onRefresh() {
        setTimeout(() => {
          this.$toast('刷新成功');
          this.isLoading = false;
          this.search=false;
        }, 500);
      },
      sell(){
        
      },
    },
}
</script>


<style scoped>
  .search{
    position:sticky;
    top:0px;
    z-index: 1;
  }
  .button{
      text-align: center;
      position:fixed;
      z-index: 1;
      bottom:60px;
      right: 10px;
  }
</style>